.mainMenu {
  font-size: 1em;
  background-color: #2c3e50;
}
.mainMenu .button {
  background: none;
  border: none;
  border-radius: 3px;
  padding: 0;
}
.mainMenu .buttonHolder {
  padding: 1.25em;
  display: flex;
}
.mainMenu .buttonHolderleft {
  justify-content: flex-start;
}
.mainMenu .buttonHolderright {
  justify-content: flex-end;
}
.mainMenu .button svg {
  vertical-align: middle;
}
.mainMenu .button :global .hamburger_svg__square,
.mainMenu .button :global .hamburger_svg__border {
  transition: all 0.2s;
  fill: #2c3e50;
}
.mainMenu .button :global .hamburger_svg__border {
  fill: #1b242f;
}
.mainMenu .button:hover :global .hamburger_svg__square {
  fill: #1b242f;
}
.mainMenu .nav {
  background-color: #2c3e50;
  display: flex;
}
.mainMenu .navDevice {
  flex-direction: column;
  position: absolute;
  width: 100%;
  left: 0;
}
.mainMenu .navleft {
  justify-content: flex-start;
}
.mainMenu .navright {
  justify-content: flex-end;
}
.mainMenu .link {
  padding: 1.25em;
  text-decoration: none;
  color: #ffffff;
  display: inline-block;
  border: 1px solid transparent;
  line-height: 2.125em;
}
.mainMenu .link:hover {
  color: #2d9031;
  background-color: #1b242f;
}
.mainMenu .link:active {
  border-color: #2d9031;
}